.view-name-container{
    height: calc(100vh - 50px);
    width: 100vw;
    background-color: transparent;
    align-items: center;
    display: flex;
    justify-content: center;
}

.background{
    height: 100%;
    width: 100%;
    position: absolute;
    background-color: #11bbff;
    z-index: -20;
}

.clouds{
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: -10;
    overflow: hidden;
}

#cloud1{
    position: absolute;
}
#cloud2{
    position: absolute;
    z-index: -12;
    opacity: 60%;
}
#cloud3{
    position: absolute;
    z-index: -14;
    opacity: 20%;
}
#cloud4{
    position: absolute;
}
#cloud5{
    position: absolute;
    z-index: -12;
    opacity: 60%;
}
#cloud6{
    position: absolute;
    z-index: -14;
    opacity: 20%;
}
#tree1{
    position: absolute;
    z-index: -11;
    transform: scale(0.4);
}
#tree2{
    position: absolute;
    z-index: -13;
    scale: 50%;
    transform: scale(0.4);
}
#tree3{
    position: absolute;
    z-index: -15;
    scale: 50%;
    transform: scale(0.4);
}
#tree4{
    position: absolute;
    z-index: -12;
    scale: 50%;
    transform: scale(-0.4,0.4);
}
#tree5{
    position: absolute;
    z-index: -14;
    scale: 50%;
    transform: scale(-0.4,0.4);
}
#tree6{
    position: absolute;
    z-index: -16;
    scale: 50%;
    transform: scale(-0.4,0.4);
}